html{font-size:calc(11px + 0.6vmin)}body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#ccc;width:100vw;height:100%;min-height:100vh}#root{width:100vw;height:100%;min-height:100vh}*{box-sizing:border-box}ul{margin:0;padding:0}li{list-style:none}h1,h3{margin:0}

.controls{display:flex;justify-content:space-between;align-items:center;width:100%;height:100%;min-width:13rem}.controls .arrow{background:var(--cellBg)}.control-buttons{position:relative;display:flex;flex-direction:column;justify-content:center;width:calc(100% - 2.25rem);height:100%}.control-buttons .btn{position:relative;display:flex;align-items:center;width:100%;min-width:-webkit-min-content;min-width:-moz-min-content;min-width:min-content;white-space:nowrap;height:calc(4.5rem / 2);padding:0rem 1rem 0 10%;border:1px solid var(--cellBorder);border-radius:.5rem;color:var(--groupText);font-weight:var(--fwL);font-family:inherit;font-size:0.8rem;background:var(--cellBg);overflow:hidden;outline:none;cursor:pointer}.control-buttons .btn span{display:flex;align-items:center;height:100%}.control-buttons .btn::after{content:'';position:absolute;width:100%;height:1px;background:transparent;bottom:0px;left:0;-webkit-transition:400ms ease-in-out;transition:400ms ease-in-out}.control-buttons .btn:hover::after{-webkit-transition:200ms ease-in-out;transition:200ms ease-in-out;background:var(--semiHighlight)}.control-buttons .btn:first-of-type{margin-bottom:0.5rem}.control-buttons .btn:first-of-type svg{height:1.4rem;width:1.4rem;margin-right:0.5rem}.control-buttons .btn:last-of-type svg{height:1.6rem;width:1.6rem;margin-right:0.7rem}.control-buttons .btn svg{color:var(--highlight)}.control-buttons .menu-container{position:absolute;width:-webkit-max-content;width:-moz-max-content;width:max-content;top:100%;left:0;z-index:10;background:var(--groupBg);border-radius:.5rem;-webkit-transform-origin:top;transform-origin:top;padding:0 0.35rem;overflow:hidden;box-shadow:0 0.1rem 0.2rem rgba(0,0,0,0.07),0 0.2rem 0.5rem rgba(0,0,0,0.07),0 0.4rem 1rem rgba(0,0,0,0.07)}.control-buttons .menu-container li{width:-webkit-max-content;width:-moz-max-content;width:max-content;min-width:100%;margin:0.35rem 0;background:var(--cellBg);border-radius:.5rem}.control-buttons .menu-container li .btn{min-width:100%;margin:0;border:0;background:transparent}

.scroll{width:100%;max-width:200px;padding:0 1rem}.scrollbar{position:relative;width:100%;background:var(--groupBg);height:.5rem;border-radius:15px;padding-right:1.5rem}.scrollbar__inner{position:relative;width:100%;height:100%;padding-right:1.5rem}.scrollbar__inner div{position:absolute;height:100%;width:1.5rem;border-radius:15px;background:var(--highlight);box-shadow:0 0 0.25rem var(--semiHighlight)}

.form{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:2rem;background:var(--mainBg);color:var(--mainText);border-radius:.5rem}.form legend h3{font-weight:400;font-size:1.5rem;color:var(--fullColor);margin-bottom:1rem}.form__field{display:flex;flex-direction:column;width:100%}.form__field label{margin-bottom:0.25rem;color:var(--fullColor);padding-left:0.5rem}.form__field input{width:100%;margin-bottom:0.5rem;background:var(--cellBg);color:inherit;font-family:inherit;font-size:inherit;font-weight:inherit;border:none;border-radius:.5rem;padding:0.5rem 1rem;margin-bottom:1rem}.form .submitBtn{width:100%;background:var(--cellBg);color:var(--fullColor);font-family:inherit;font-size:inherit;font-weight:inherit;border:none;padding:0.5rem 1rem;margin-top:0.75rem;border-radius:.5rem;max-width:12.5rem;letter-spacing:1px;box-shadow:0 0.04rem 0.08rem rgba(0,0,0,0.07),0 0.08rem 0.16rem rgba(0,0,0,0.07),0 0.16rem 0.32rem rgba(0,0,0,0.07),0 0.32rem 0.64rem rgba(0,0,0,0.07),0 0.64rem 1.28rem rgba(0,0,0,0.07),0 1.28rem 2.4rem rgba(0,0,0,0.07);-webkit-transition:200ms ease-in-out;transition:200ms ease-in-out;cursor:pointer}.form .submitBtn:hover{-webkit-transition:400ms ease-in-out;transition:400ms ease-in-out;box-shadow:0 0.04rem 0.08rem rgba(0,0,0,0.04),0 0.08rem 0.16rem rgba(0,0,0,0.04),0 0.16rem 0.32rem rgba(0,0,0,0.04),0 0.32rem 0.64rem rgba(0,0,0,0.04),0 0.64rem 1.28rem rgba(0,0,0,0.04)}

.modal{position:fixed;height:100vh;width:100vw;top:0;left:0;z-index:200;display:flex;align-items:center;justify-content:center}.modal__inner{position:relative;display:flex;flex-direction:column;width:100%;max-width:500px;z-index:+1;border-radius:.5rem}.modal__inner .modal__close{display:inline-block;position:absolute;right:0;top:0;padding:0.5rem;font-size:1.2rem;font-weight:600;cursor:pointer}.modal__inner .modal__close svg{width:1.75rem;height:1.75rem;color:var(--fullColor);opacity:0.8}.backdrop{position:fixed;top:0;left:0;height:100vh;width:100vw;z-index:190}.modal__header h1{margin:0;font-size:1.25rem}.modal__content{padding:1rem}.modal__actions{display:flex;justify-content:flex-end;padding:1rem}

